<template>
  <div class="hello">
    <connectivity-banner data-qa="connectivity-banner" />
    <div class="hello_top">
      <icon data-qa="glovo_logo" name="logo-name"></icon>
    </div>
    <div class="hello_content">
      <div class="hello_content_form">
        <div
          v-if="logError"
          id="log_error_id"
          data-qa="error-msg"
          class="hello_log_error"
        >
          {{ $t('permissions.forgot') }}
        </div>
        <div class="hello_content_form_pair">
          <div class="hello_content_form_hint">
            {{ $t('common.email') }}
          </div>
          <input
            v-model="partner.username"
            type="email"
            placeholder="partner@glovoapp.com"
            data-qa="input-email"
          />
        </div>
        <div class="hello_content_form_pair">
          <div class="hello_content_form_hint">
            {{ $t('hello.password') }}
          </div>
          <input
            v-model="partner.password"
            type="password"
            placeholder="********"
            data-qa="input-password"
            @keyup.enter="onSubmit"
          />
        </div>
      </div>
      <div class="hello_content_form_forgot">
        {{ $t('hello.forgot') }}
      </div>
      <div class="hello_content_form_btn">
        <glovo-btn
          :text="$t('hello.login')"
          type="white"
          :uppercase="true"
          :bold="true"
          data-qa="sign-in-submit-button"
          @clack="onSubmit"
        ></glovo-btn>
      </div>
    </div>
  </div>
</template>

<script>
import Icon from 'common/Icon'
import GlovoBtn from 'common/GlovoBtn'
import { mapActions, mapGetters } from 'vuex'
import { IS_AUTHENTICATED_GETTER, LOG_IN } from '../../store'
import ConnectivityBanner from '@/components/main/top/ConnectivityBanner.vue'

export default {
  name: 'Hello',
  components: {
    Icon,
    GlovoBtn,
    ConnectivityBanner
  },
  data() {
    return {
      partner: {
        password: '',
        username: ''
      },
      logError: false
    }
  },
  beforeMount() {
    if (this.isAuthenticated()) {
      this.$router.push({ name: 'blank' })
    }
  },
  methods: {
    ...mapActions('authentication', { logIn: LOG_IN }),
    ...mapGetters('authentication', {
      isAuthenticated: IS_AUTHENTICATED_GETTER
    }),
    async onSubmit() {
      this.logError = false
      try {
        await this.logIn(this.partner)
        this.$router.push({ name: 'blank' })
      } catch (error) {
        this.logError = true
      }
    }
  }
}
</script>
